<template>
    <div :class="style.headerDiv" class="header">       
        <img src="static/assets/logo.png" :class="style.titleImg">
        <div :class="style.header">
            <el-menu :default-active="activeMenu" router class="el-menu-demo" mode="horizontal" @select="handleSelect" background-color="#545c64"
            text-color="#fff" active-text-color="#ffd04b">
                <el-menu-item index="home">Home</el-menu-item>
                <el-menu-item index="ads">Top Advertisers</el-menu-item>
                <el-menu-item index="geo">Top Target Countries</el-menu-item>
            </el-menu>
        </div>
    </div>
</template>
<script>
import style from '@/css/main.css'
export default {
    data(){
        return{
           style,    
           activeMenu: 'home',       
        }
    },
    methods:{
        handleSelect(){

        }
    },
    watch:{
        $route(){
           if(this.$route){
             this.activeMenu = this.$route.name;
           }
        }
    },
    mounted() {
        if(this.$route){
            this.activeMenu = this.$route.name;
        }
    },
}
</script>




